@tailwind base;
@tailwind components;
@tailwind utilities;


.root-body {

  --tw-gradient-from: #dcfaca;
  --tw-gradient-to: #c1eff7;
  --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
  background: linear-gradient(45deg, var(--tw-gradient-stops));
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;

  color-scheme: light dark;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

.dialogue {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: rgba(248, 249, 250, 0.5);
  /* bg-white bg-opacity-50 rounded-lg shadow-md */
  /* height:50%;  */
  /* overflow-y:scroll; */
}
.promptcard {
  max-height:120px;
  min-height:120px;
}
.promptcard:hover {
  border-color: #8b8c92;
}
.sendSVG {
  max-height:24px;
  min-height:24px;
  max-width:24px;
  min-width:24px;
  
}
.inputarea{
  border-top: 2px;
  --tw-gradient-from: #dcfaca;
  --tw-gradient-to: #c1eff7;
  --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
  background: linear-gradient(174deg,var(--tw-gradient-stops));
}
.own-button {
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  background-color: #1a1a1a;
  cursor: pointer;
  transition: border-color 0.25s;
}

.own-button:hover {
  border-color: #646cff;
}

.own-button:focus,
.own-button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

@media (prefers-color-scheme: light) {

  .own-button {
    background-color: #f9f9f9;
  }
}